  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 15px;
}

.check-update-btn:hover {
  background: #764ba2;
  transform: translateY(-2px);
}

/* Version Cards (enhanced card style) */
.versions-list {
  display: flex;
  flex-direction: column;
  gap: 18px;
  margin-top: 12px;
}

.version-card {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 18px 22px 18px 64px;
  border-radius: 12px;
  background: linear-gradient(180deg, rgba(255,255,255,0.015), rgba(0,0,0,0.02));
  border: 1px solid rgba(255,255,255,0.03);
  box-shadow: 0 10px 30px rgba(0,0,0,0.6);
  transition: transform 180ms ease, box-shadow 180ms ease;
}

.version-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 18px 45px rgba(0,0,0,0.65);
}

.version-card::before {
  content: '';
  position: absolute;
  left: 16px;
  top: 12px;
  bottom: 12px;
  width: 8px;
  border-radius: 6px;
  background: linear-gradient(180deg, #999, #666);
}

.version-header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: 12px;
  margin-bottom: 8px;
}

.version-header h3 {
  font-family: 'Oswald', sans-serif;
  font-size: 20px;
  color: #ffffff;
  margin: 0;
  letter-spacing: 0.4px;
}

.version-card.beta .version-header h3::before {
  content: 'üöÄ';
  margin-right: 8px;
}
.version-card.alpha .version-header h3::before {
  content: 'üß™';
  margin-right: 8px;
}
.version-card.indev .version-header h3::before {
  content: 'üîß';
  margin-right: 8px;
}
.version-card.release .version-header h3::before {
  content: '‚úÖ';
  margin-right: 8px;
}

.release-date {
  font-size: 13px;
  color: #cfcfcf;
  background: rgba(255,255,255,0.02);
  padding: 6px 10px;
  border-radius: 8px;
}

.version-brief {
  color: #e6e6e6;
  font-size: 15px;
  margin: 8px 0 14px 0;
  line-height: 1.5;
  font-style: italic;
}

.version-actions {
  display: flex;
  gap: 10px;
}

.version-actions .btn {
  padding: 8px 14px;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 800;
  font-size: 13px;
  color: #111;
  box-shadow: 0 6px 16px rgba(0,0,0,0.35);
}

.version-card.beta::before { background: linear-gradient(180deg,#ffb300,#ff8f00); }
.version-card.beta { border-color: rgba(255,180,0,0.08); }
.version-card.beta .version-header h3 { color: #ffecb3; }
.version-card.beta .version-actions .btn { background: linear-gradient(90deg,#ffb300,#ffd54f); }

.version-card.alpha::before { background: linear-gradient(180deg,#8ea1ff,#7b61d6); }
.version-card.alpha { border-color: rgba(123,97,214,0.06); }
.version-card.alpha .version-header h3 { color: #dfe8ff; }
.version-card.alpha .version-actions .btn { background: linear-gradient(90deg,#9fb6ff,#b89bff); }

@media (min-width: 700px) {
  .versions-list { gap: 20px; }
  .version-card { padding: 22px 26px 22px 72px; border-radius: 14px; }
  .version-header h3 { font-size: 22px; }
}

/* Feedback Form */
.feedback-form {
  background: #f9f9f9;
  padding: 25px;
  border-radius: 12px;
  margin: 20px 0;
}

.submit-feedback-btn {
  padding: 15px 30px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.submit-feedback-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(102, 126, 234, 0.4);
}

/* Button Group */
.button-group {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  margin-top: 20px;
}

.submit-feedback-btn.local-btn {
  background: linear-gradient(135deg, #667eea, #764ba2);
}

.submit-feedback-btn.online-btn {
  background: linear-gradient(135deg, #764ba2, #f093fb);
}

.btn-icon {
  font-size: 18px;
}

.feedback-status {
  margin-top: 15px;
  padding: 15px;
  border-radius: 8px;
  text-align: center;
  font-weight: 600;
  display: none;
}

.feedback-status.success {
  display: block;
  background: #d4edda;
  border: 2px solid #28a745;
  color: #155724;
}

.feedback-status.error {
  display: block;
  background: #f8d7da;
  border: 2px solid #dc3545;
  color: #721c24;
}

/* Email Service Modal */
.email-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-out;
}

.modal-content {
  background: white;
  border-radius: 15px;
  box-shadow: 0 10px 50px rgba(0, 0, 0, 0.3);
  max-width: 500px;
  width: 90%;
  overflow: hidden;
  animation: slideUp 0.3s ease-out;
}

.modal-header {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h3 {
  margin: 0;
  font-size: 20px;
}

.modal-close {
  background: none;
  border: none;
  color: white;
  font-size: 28px;
  cursor: pointer;
  transition: all 0.2s ease;
  padding: 0;
  line-height: 1;
}

.modal-close:hover {
  transform: rotate(90deg);
}

.modal-body {
  padding: 25px;
}

.modal-body p {
  margin-bottom: 20px;
  color: #666;
  text-align: center;
}

.email-options {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
}

.email-option {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  padding: 15px 20px;
  border-radius: 10px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  color: white;
  border: 2px solid transparent;
}

.gmail-btn {
  background: linear-gradient(135deg, #EA4335, #C5221F);
}

.gmail-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(234, 67, 53, 0.4);
}

.outlook-btn {
  background: linear-gradient(135deg, #0078D4, #004B87);
}

.outlook-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 120, 212, 0.4);
}

.yahoo-btn {
  background: linear-gradient(135deg, #7C2AE8, #510FA9);
}

.yahoo-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(124, 42, 232, 0.4);
}

.service-icon {
  font-size: 20px;
}

.service-name {
  font-size: 16px;
}
@media (max-width: 768px) {
  .main-title {
    font-size: 32px;
  }

  .main-title span {
    display: block;
    margin: 5px 0;
  }

  .title-wrapper {
    padding: 30px 20px;
  }

  .main-content {
    padding: 25px;
  }

  .response-message.user,
  .response-message.assistant {
    max-width: 100%;
  }

  .input-field,
  .submit-btn {
    padding: 12px 15px;
    font-size: 14px;
  }

  .navigation {
    gap: 8px;
  }

  .nav-btn {
    padding: 10px 15px;
    font-size: 12px;
  }

  .content-box h2 {
    font-size: 22px;
  }

  .form-group {
    margin-bottom: 15px;
  }
}

/* Dark theme overrides */
:root{
  --bg-900:#070709;
  --bg-800:#0b0b0d;
  --panel:#0f1113;
  --muted:#bfc7d1;
  --accent-orange-1:#ff8a00;
  --accent-orange-2:#ffb300;
  --accent-blue-1:#4f7ef2;
  --accent-blue-2:#6f7efb;
}

body{
  background: linear-gradient(180deg,var(--bg-900),var(--bg-800));
  color: #e6e6e6;
}

.title-wrapper{
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(0,0,0,0.14));
  border: 1px solid rgba(255,255,255,0.03);
}

.main-content{
  background: linear-gradient(180deg, rgba(255,255,255,0.01), rgba(0,0,0,0.25));
  border: 1px solid rgba(255,255,255,0.03);
}

.nav-btn{ background: rgba(255,255,255,0.02); border-color: rgba(255,255,255,0.03); }
.nav-btn:hover{ background: rgba(255,255,255,0.05); color: #fff }
.nav-btn.active{ background: linear-gradient(90deg,var(--accent-orange-1),var(--accent-orange-2)); color:#111; border-color: rgba(255,180,0,0.08) }

.form-input, .form-textarea, .strategy-form, .feedback-form{ background: #0d0f11; color: var(--muted); border-color: rgba(255,255,255,0.03) }
.response-message.assistant{ background: #0f1316; color: var(--muted); border-left-color: var(--accent-blue-1) }

/* Scope version card styles to only the version page so other pages don't use the card treatment */
#version-page .versions-list{ display:flex; flex-direction:column; gap:18px; margin-top:12px }

#version-page .version-card{
  position:relative;
  display:flex;
  flex-direction:column;
  padding:18px 22px 18px 64px;
  border-radius:12px;
  background: linear-gradient(180deg, rgba(255,255,255,0.014), rgba(0,0,0,0.45));
  border:1px solid rgba(255,255,255,0.03);
  box-shadow:0 14px 40px rgba(0,0,0,0.7);
  color: #e6e6e6;
  transition: transform 180ms ease, box-shadow 180ms ease;
}

#version-page .version-card:hover{ transform:translateY(-8px); box-shadow:0 28px 60px rgba(0,0,0,0.75) }

#version-page .version-card::before{ content:''; position:absolute; left:18px; top:12px; bottom:12px; width:10px; border-radius:8px; background:linear-gradient(180deg,#555,#222) }

#version-page .version-header h3{ font-family:'Racing Sans One','Audiowide',sans-serif; font-size:20px; color: #fff; letter-spacing:1px }
#version-page .release-date{ color: var(--muted); background: rgba(255,255,255,0.02) }
#version-page .version-brief{ color:#d0d6dd; font-style:italic }

/* Modifiers: beta uses orange accent, alpha uses blue accent */
#version-page .version-card.beta::before{ background: linear-gradient(180deg,var(--accent-orange-1),var(--accent-orange-2)) }
#version-page .version-card.beta{ border-color: rgba(255,160,0,0.08) }
#version-page .version-card.beta .version-header h3{ color: #fff3e6 }
#version-page .version-card.beta .version-actions .btn{ background: linear-gradient(90deg,var(--accent-orange-2), #ffd76a); color:#111 }

#version-page .version-card.alpha::before{ background: linear-gradient(180deg,var(--accent-blue-1),var(--accent-blue-2)) }
#version-page .version-card.alpha{ border-color: rgba(111,126,251,0.06) }
#version-page .version-card.alpha .version-header h3{ color: #eaf0ff }
#version-page .version-card.alpha .version-actions .btn{ background: linear-gradient(90deg,var(--accent-blue-1), var(--accent-blue-2)); color:#111 }

/* Modifiers: indev uses grey accent, release uses green accent */
#version-page .version-card.indev::before{ background: linear-gradient(180deg,#555,#222) }
#version-page .version-card.indev{ border-color: rgba(136,136,136,0.06) }
#version-page .version-card.indev .version-header h3{ color: #cccccc }
#version-page .version-card.indev .version-actions .btn{ background: linear-gradient(90deg,#888,#aaa); color:#111 }

#version-page .version-card.release::before{ background: linear-gradient(180deg,#28a745,#1e7e34) }
#version-page .version-card.release{ border-color: rgba(40,167,69,0.08) }
#version-page .version-card.release .version-header h3{ color: #d4ffd4 }
#version-page .version-card.release .version-actions .btn{ background: linear-gradient(90deg,#28a745,#34ce57); color:#111 }

/* make action buttons stand out on dark bg */
#version-page .version-actions .btn{ padding:8px 14px; border-radius:8px; font-weight:700; box-shadow:0 8px 22px rgba(0,0,0,0.6) }

/* High-specificity overrides to ensure dark theme wins over bundled CSS */
body .title-wrapper{ background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(0,0,0,0.18)); border-color: rgba(255,255,255,0.03) }
body .main-content{ background: linear-gradient(180deg, rgba(0,0,0,0.28), rgba(0,0,0,0.48)); border-color: rgba(255,255,255,0.02) }
body .nav-btn{ background: rgba(255,255,255,0.02) !important; border-color: rgba(255,255,255,0.02) }
body .nav-btn:hover{ background: rgba(255,255,255,0.06) !important }
body .nav-btn.active{ background: linear-gradient(90deg,var(--accent-orange-1),var(--accent-orange-2)) !important; color:#111 !important }
body .form-input, body .form-textarea, body .strategy-form, body .feedback-form{ background: #0d0f11 !important; color: var(--muted) !important; border-color: rgba(255,255,255,0.02) !important }
body .content-box h2{ color: var(--accent-orange-2) !important }

/* Motorsports-themed News and About Us Sections */
.news-section, .about-section {
  margin: 30px 0;
  animation: fadeInUp 0.8s ease-out;
}

.news-section h3, .about-section h3 {
  font-family: 'Oswald', sans-serif;
  font-size: 28px;
  color: #ffb300;
  margin-bottom: 25px;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-shadow: 0 2px 10px rgba(255, 179, 0, 0.3);
  position: relative;
  padding-left: 40px;
}

.news-section h3::before, .about-section h3::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  background: linear-gradient(135deg, #e53935, #ffb300);
  border-radius: 50%;
  box-shadow: 0 4px 15px rgba(255, 179, 0, 0.4);
}

/* News Grid Styling */
.news-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 25px;
  margin-top: 20px;
}

.news-card {
  background: linear-gradient(135deg, rgba(255,255,255,0.03), rgba(0,0,0,0.3));
  border: 1px solid rgba(255,255,255,0.08);
  border-radius: 15px;
  padding: 20px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
}

.news-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent);
  transition: left 0.6s ease;
}

.news-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0,0,0,0.6);
  border-color: rgba(255, 179, 0, 0.3);
}

.news-card:hover::before {
  left: 100%;
}

.news-image {
  width: 100%;
  aspect-ratio: 1 / 1;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 15px;
  position: relative;
}

.news-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.news-card:hover .news-image img {
  transform: scale(1.05);
}

.news-content h4 {
  font-family: 'Oswald', sans-serif;
  font-size: 18px;
  color: #fff;
  margin-bottom: 8px;
  line-height: 1.3;
}

.news-date {
  display: inline-block;
  background: linear-gradient(135deg, #e53935, #ff6b00);
  color: white;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.news-content p {
  color: #d0d6dd;
  line-height: 1.6;
  font-size: 14px;
  margin: 0;
}

/* About Us Section Styling */
.about-content {
  background: linear-gradient(135deg, rgba(255,255,255,0.02), rgba(0,0,0,0.4));
  border: 1px solid rgba(255,255,255,0.06);
  border-radius: 15px;
  padding: 30px;
  backdrop-filter: blur(10px);
}

.company-info h4 {
  font-family: 'Oswald', sans-serif;
  font-size: 22px;
  color: #ffb300;
  margin-bottom: 15px;
  text-transform: uppercase;
}

.mission {
  font-size: 18px;
  color: #e53935;
  font-weight: 600;
  margin-bottom: 15px;
  font-style: italic;
  border-left: 4px solid #ffb300;
  padding-left: 15px;
}

.description {
  color: #d0d6dd;
  line-height: 1.6;
  margin-bottom: 25px;
}

.team-section, .features-section, .contact-section {
  margin-top: 30px;
}

.team-section h4, .features-section h4, .contact-section h4 {
  font-family: 'Oswald', sans-serif;
  font-size: 20px;
  color: #ffb300;
  margin-bottom: 20px;
  text-transform: uppercase;
  border-bottom: 2px solid rgba(255, 179, 0, 0.3);
  padding-bottom: 8px;
}

.team-grid {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.team-member {
  display: flex;
  width: 100%;
  min-height: 200px;
  background: rgba(255,255,255,0.03);
  border: 1px solid rgba(255,255,255,0.05);
  border-radius: 0;
  overflow: hidden;
  transition: all 0.3s ease;
  position: relative;
}

.team-member:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(0,0,0,0.4);
  border-color: rgba(255, 179, 0, 0.2);
}

.member-image {
  flex: 0 0 50%;
  position: relative;
  overflow: hidden;
  clip-path: polygon(0 0, 100% 0, 85% 100%, 0% 100%);
}

.member-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.team-member:hover .member-image img {
  transform: scale(1.05);
}

.member-info {
  flex: 0 0 50%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.member-info h5 {
  font-family: 'Oswald', sans-serif;
  font-size: 20px;
  color: #fff;
  margin-bottom: 10px;
  text-transform: uppercase;
}

.member-date {
  display: inline-block;
  background: rgba(255,255,255,0.1);
  color: #ffb300;
  padding: 5px 15px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 15px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.member-info p {
  color: #d0d6dd;
  font-size: 14px;
  line-height: 1.6;
  margin: 0;
}

/* Alternate layout for even-numbered team members (image on right) */
.team-member:nth-child(even) {
  flex-direction: row-reverse;
}

.team-member:nth-child(even) .member-image {
  clip-path: polygon(15% 0, 100% 0, 100% 100%, 0% 100%);
}

.features-list {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 12px;
}

.features-list li {
  background: rgba(255,255,255,0.03);
  padding: 12px 15px;
  border-radius: 8px;
  color: #d0d6dd;
  font-size: 14px;
  transition: all 0.3s ease;
  border-left: 4px solid #e53935;
}

.features-list li:hover {
  background: rgba(255,255,255,0.06);
  transform: translateX(5px);
  color: #fff;
}

.contact-info {
  background: rgba(255,255,255,0.02);
  border-radius: 10px;
  padding: 20px;
  border: 1px solid rgba(255,255,255,0.05);
}

.contact-info p {
  color: #d0d6dd;
  margin-bottom: 10px;
  font-size: 14px;
}

.contact-info strong {
  color: #ffb300;
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .news-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .team-grid {
    gap: 20px;
  }
  
  .team-member {
    flex-direction: column !important;
    min-height: auto;
  }
  
  .member-image {
    flex: 0 0 250px;
    clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%) !important;
  }
  
  .member-info {
    flex: 1;
    padding: 20px;
  }
  
  .features-list {
    grid-template-columns: 1fr;
  }
  
  .news-section h3, .about-section h3 {
    font-size: 24px;
    padding-left: 35px;
  }
  
  .news-section h3::before, .about-section h3::before {
    width: 25px;
    height: 25px;
  }
  
  .about-content {
    padding: 20px;
  }
}

/* Driver Roll Page - Motorsports Themed Styles */
.driver-roll-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

.driver-roll-title {
  font-family: 'Racing Sans One', 'Audiowide', sans-serif;
  font-size: 36px;
  color: #ffb300;
  text-align: center;
  margin-bottom: 30px;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 0 4px 15px rgba(255, 179, 0, 0.4);
  position: relative;
}

.driver-roll-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
