.stint-controls {
  display: flex;
  gap: 15px;
  margin-bottom: 25px;
  flex-wrap: wrap;
}

.add-stint-btn {
  padding: 12px 25px;
  background: linear-gradient(135deg, #e53935, #ff6d00);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  flex: 1;
  min-width: 200px;
}

.add-stint-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(229, 57, 53, 0.4);
  background: linear-gradient(135deg, #ff1744, #ff9100);
}

.stint-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 20px;
}

.stint-card {
  background: linear-gradient(135deg, rgba(255,255,255,0.03), rgba(0,0,0,0.3));
  border: 1px solid rgba(255,255,255,0.08);
  border-radius: 10px;
  padding: 15px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
}

.stint-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 179, 0, 0.1), transparent);
  transition: left 0.6s ease;
}

.stint-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0,0,0,0.5);
  border-color: rgba(255, 179, 0, 0.3);
}

.stint-card:hover::before {
  left: 100%;
}

.stint-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.stint-number {
  font-family: 'Oswald', sans-serif;
  font-size: 18px;
  color: #ffb300;
  text-transform: uppercase;
}

.remove-stint-btn {
  padding: 6px 12px;
  background: linear-gradient(135deg, #f44336, #d32f2f);
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.remove-stint-btn:hover {
  background: linear-gradient(135deg, #f55549, #e53935);
  transform: scale(1.05);
}

.stint-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.stint-form .form-group {
  margin-bottom: 0;
}

.stint-form label {
  display: block;
  margin-bottom: 8px;
  color: #d0d6dd;
  font-weight: 600;
  font-size: 14px;
}

.stint-form select,
.stint-form input {
  width: 100%;
  padding: 10px 12px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  background: rgba(0, 0, 0, 0.3);
  color: #e6e6e6;
  font-size: 14px;
  font-family: inherit;
  transition: all 0.3s ease;
}

.stint-form select:focus,
.stint-form input:focus {
  outline: none;
  border-color: #ffb300;
  box-shadow: 0 0 0 3px rgba(255, 179, 0, 0.2);
}

.push-conserve-container {
  grid-column: span 2;
  display: flex;
  gap: 10px;
}

.push-conserve-container .form-group {
  flex: 1;
}
.calculation-mode-toggle {
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 25px;
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  gap: 15px;
  flex-wrap: wrap;
}

.calculation-mode-toggle label {
  font-family: 'Oswald', sans-serif;
  font-size: 16px;
  color: #ffb300;
  font-weight: 600;
  text-transform: uppercase;
  white-space: nowrap;
}

.toggle-buttons {
  display: flex;
  gap: 8px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  padding: 4px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.toggle-btn {
  padding: 8px 16px;
  background: transparent;
  color: #d0d6dd;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.toggle-btn:hover {
  background: rgba(255, 255, 255, 0.05);
}

.toggle-btn.active {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.calculation-mode-toggle .hint {
  font-size: 12px;
  color: #a0a0a0;
  font-style: italic;
  margin-left: auto;
}
/* Strategy Results Section */
.results-section {
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 15px;
  padding: 25px;
  margin-top: 30px;
  backdrop-filter: blur(10px);
}

.results-section h3 {
  font-family: 'Oswald', sans-serif;
  font-size: 24px;
  color: #ffb300;
  margin-bottom: 20px;
  text-transform: uppercase;
}

.calculate-btn {
  padding: 15px 30px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: block;
  width: 100%;
  margin-bottom: 25px;
}

.calculate-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(102, 126, 234, 0.4);
  background: linear-gradient(135deg, #764ba2, #667eea);
}

/* Lap Time Graph */
.graph-container {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 25px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.graph-title {
  font-family: 'Oswald', sans-serif;
  font-size: 18px;
  color: #ffb300;
  margin-bottom: 15px;
  text-align: center;
  text-transform: uppercase;
}

.lap-time-chart {
  height: 300px;
  width: 100%;
}

/* Cumulative Delta */
.cumulative-delta-container {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  padding: 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.cumulative-delta-title {
  font-family: 'Oswald', sans-serif;
  font-size: 18px;
  color: #ffb300;
  margin-bottom: 15px;
  text-align: center;
  text-transform: uppercase;
}

.cumulative-delta-value {
  font-family: 'Racing Sans One', 'Audiowide', sans-serif;
  font-size: 32px;
  color: #fff;
  text-align: center;
  font-weight: bold;
  letter-spacing: 1px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .strategy-container {
    padding: 15px;
  }
  
  .strategy-title {
    font-size: 28px;
  }
  
  .stint-form {
    grid-template-columns: 1fr;
  }
  
  .push-conserve-container {
    grid-column: span 1;
  }
  
  .stint-cards {
    grid-template-columns: 1fr;
  }
  
  .stint-controls {
    flex-direction: column;
  }
  
  .add-stint-btn {
    min-width: auto;
  }
}

/* Color coding for lap time deltas */
.lap-time-positive {
  color: #4CAF50;
}

.lap-time-negative {
  color: #F44336;
}

.lap-time-neutral {
  color: #FFC107;
}

/* Lap Bar Styles */
.lap-bar {
  min-width: 8px;
  background: #4CAF50;
  transition: all 0.3s ease;
  cursor: pointer;
  border-radius: 4px 4px 0 0;
}

.lap-bar:hover {
  opacity: 0.8;
  transform: scale(1.1);
}

/* Lap Details Table */
.lap-details {
  margin-top: 1.5rem;
}

.lap-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.lap-header, .lap-row {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  padding: 0.75rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.lap-header {
  background: rgba(255, 255, 255, 0.1);
  font-weight: bold;
  color: #ffb300;
}

.lap-row:nth-child(even) {
  background: rgba(255, 255, 255, 0.05);
}

/* Responsive design for strategy page */
@media (max-width: 768px) {
  .lap-header, .lap-row {
    grid-template-columns: repeat(3, 1fr);
    font-size: 0.9rem;
  }
}
/* FAQ Page Styles */
.faq-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.faq-title {
  font-family: 'Oswald', sans-serif;
  font-size: 3rem;
  color: #ffb300;
  text-align: center;
  margin-bottom: 2rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.faq-subtitle {
  font-family: 'Racing Sans One', 'Audiowide', sans-serif;
  font-size: 1.2rem;
  color: #ccc;
  text-align: center;
  margin-bottom: 3rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

/* FAQ Categories */
.faq-categories {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 3rem;
}

.category-btn {
  padding: 0.8rem 1.5rem;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 25px;
  font-family: 'Oswald', sans-serif;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.category-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.category-btn.active {
  background: linear-gradient(135deg, #ffb300, #ff8c00);
  box-shadow: 0 0 20px rgba(255, 179, 0, 0.5);
}

/* FAQ Cards */
.faq-cards {
  display: grid;
  gap: 1.5rem;
  max-width: 900px;
  margin: 0 auto;
}

.faq-card {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.02));
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 0;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  overflow: hidden;
}

.faq-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  border-color: rgba(255, 179, 0, 0.3);
}

.faq-card.expanded {
  background: linear-gradient(135deg, rgba(255, 179, 0, 0.1), rgba(255, 140, 0, 0.05));
}

.faq-question {
  padding: 1.5rem;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(0, 0, 0, 0.2);
  transition: background 0.3s ease;
}

.faq-question:hover {
  background: rgba(255, 179, 0, 0.1);
}

.faq-question h3 {
  font-family: 'Oswald', sans-serif;
  font-size: 1.3rem;
  color: #ffb300;
  margin: 0;
  flex: 1;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.expand-icon {
  font-size: 1.5rem;
  color: #ffb300;
  transition: transform 0.3s ease;
}

.faq-card.expanded .expand-icon {
  transform: rotate(180deg);
}

.faq-answer {
  padding: 0 1.5rem;
  max-height: 0;
  overflow: hidden;
  transition: all 0.3s ease;
  background: rgba(0, 0, 0, 0.1);
}

.faq-card.expanded .faq-answer {
  padding: 0 1.5rem 1.5rem;
  max-height: 500px;
}

.faq-answer p {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  color: #e0e0e0;
  line-height: 1.6;
  margin: 0;
}

.faq-category {
  display: inline-block;
  padding: 0.3rem 0.8rem;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-top: 0.5rem;
}

/* FAQ Loading and Error States */
.faq-loading {
  text-align: center;
  padding: 3rem;
  color: #ffb300;
  font-family: 'Oswald', sans-serif;
  font-size: 1.2rem;
}

.faq-error {
  text-align: center;
  padding: 3rem;
  color: #f44336;
  font-family: 'Oswald', sans-serif;
  font-size: 1.2rem;
  background: rgba(244, 67, 54, 0.1);
  border-radius: 15px;
  border: 1px solid rgba(244, 67, 54, 0.3);
}

/* FAQ Search */
.faq-search {
  max-width: 500px;
  margin: 0 auto 3rem;
  position: relative;
}

.faq-search input {
  width: 100%;
  padding: 1rem 1.5rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  color: white;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.faq-search input:focus {
  outline: none;
  border-color: #ffb300;
  box-shadow: 0 0 15px rgba(255, 179, 0, 0.3);
}

.faq-search input::placeholder {
  color: #888;
}

/* FAQ Animation */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.faq-card {
  animation: fadeInUp 0.5s ease forwards;
}

/* Responsive Design for FAQ Page */
@media (max-width: 768px) {
  .faq-container {
    padding: 1rem;
  }
  
  .faq-title {
    font-size: 2rem;
  }
  
  .faq-subtitle {
    font-size: 1rem;
    padding: 0 1rem;
  }
  
  .faq-categories {
    gap: 0.5rem;
  }
  
  .category-btn {
    padding: 0.6rem 1rem;
    font-size: 0.9rem;
  }
  
  .faq-question h3 {
    font-size: 1.1rem;
  }
  
  .faq-answer p {
    font-size: 0.9rem;
  }
  
  .faq-search {
    margin: 0 auto 2rem;
    padding: 0 1rem;
  }
}

/* FAQ Empty State */
.faq-empty {
  text-align: center;
  padding: 3rem;
  color: #888;
  font-family: 'Oswald', sans-serif;
  font-size: 1.2rem;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}
